<template>
  <div id="app" :class="{ showbar: currentPlaySong }">
    <div class="m-topbar">
      <div class="topfix">
      <h1 class="toplf">网易云音乐</h1>
      </div>
      <div class="topright">
        <span>下载APP</span>
      </div>
    </div>
    <!-- 导航 -->
    <ul id="nav" v-if="!$route.meta.hideNav">
      <li><router-link to="/">推荐音乐</router-link></li>
      <li><router-link to="/hot">热歌榜</router-link></li>
      <li><router-link to="/search">搜索</router-link></li>
    </ul>

    <!-- 路由出口 -->
    <Transition>
      <router-view
        @change-current-play-song="changeCurrentPlaySong"
        @change-current-play-song-list="currentPlaySongList = $event"
      />
    </Transition>

    <!--  -->

    <Play
      v-if="currentPlaySong"
      :currentPlaySong="currentPlaySong"
      :currentPlaySongList="currentPlaySongList"
      @change-current-play-song="changeCurrentPlaySong"
      @prev-song="prevSong"
      @next-song="nextSong"
      @remove-song="removeSong"
    ></Play>
  </div>
</template>

<script>
import Play from "@/components/Play.vue";
export default {
  components: {
    Play,
  },
  data: function () {
    return {
     currentPlaySong: JSON.parse(
        window.localStorage.getItem("currentPlaySong")
      ),
      currentPlaySongList: window.localStorage.getItem("currentPlaySongList")
        ? JSON.parse(window.localStorage.getItem("currentPlaySongList"))
        : [],
    };
  },
  computed: {
    currentSongIndex: function () {
      return this.currentPlaySongList.findIndex(
        (e) => e.id === this.currentPlaySong.id
      );
    },
  },
  methods: {
    changeCurrentPlaySong: function (payload) {
      // console.log(payload.id, payload.name);
      this.currentPlaySong = payload;
    },
    prevSong: function () {
      this.currentPlaySong = this.currentPlaySongList[
        this.currentSongIndex <= 0
          ? this.currentPlaySongList.length - 1
          : this.currentSongIndex - 1
      ];
    },
    nextSong: function () {
      this.currentPlaySong = this.currentPlaySongList[
        this.currentSongIndex >= this.currentPlaySongList.length - 1
          ? 0
          : this.currentSongIndex + 1
      ];
    },
    removeSong:function(e){
      this.currentPlaySongList=this.currentPlaySongList.filter(item => item.id !== e)
    }
  },
  watch:{
    currentPlaySong: function (n) {
      window.localStorage.setItem("currentPlaySong", JSON.stringify(n));
    },
    currentPlaySongList: function (n) {
      window.localStorage.setItem("currentPlaySongList", JSON.stringify(n));
    },
  }
};
</script>

<style lang="less">
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  &.showbar {
    margin-bottom: 50px;
  }
}
.m-topbar{
  width:100%;
  height: 84px;
  background: #d43c33;
  position: relative;
  left: 0;
  top: 0;
}
.topfix{
  padding: 0 10px;
  line-height: 84px;
  h1{
    color:white;
    font-size: 20px;
    margin-left: 10px;
  }
}
.topright{
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 20px;
  span{
    display: block;
    color:#dd001b;
    background-color:white;
    text-align: center;
    width: 100px;
    height: 40px;
    line-height: 40px;
    border-radius: 20px;
    font-size: 18px;
  }
}
#nav {
  display: flex;
  // border-bottom: 1px solid blue;
  box-shadow: 0 -1px 0px 0px #7b7b7b5c inset;

  li {
    flex: 1;
    text-align: center;
    a {
      color: #2c3e50;
      text-decoration: none;
      line-height: 40px;
      padding: 0 5px;
      display: inline-block;
      font-size: 14px;

      &.router-link-exact-active {
        color: #dd001b;
        border-bottom: 2px solid #dd001b;
      }
    }
  }
}
</style>
