<template>
  <section class="song-list">
    <div class="top">
      <div
        class="header"
        :style="{
          backgroundImage: 'url(' + datail.coverImgUrl + ')',
        }"
      >
        <div class="leftpic">
          <img :src="datail.coverImgUrl | imgurl" alt="" />
          <span class="numing">{{ datail.playCount | parsePalyCount }}</span>
        </div>
        <div class="rightname">
          <h2 class="name">{{ datail.name }}</h2>
        </div>
      </div>
      <div class="song-list-content">
      <h4>歌曲列表</h4>
      <ul>
        <PlayListMusic
         v-for="(item,index) in datail.tracks"
         :key="item.id"
         :item="item"
         :index="index"
         @change-current-play-song="
          $emit('change-current-play-song', $event);
          $emit('change-current-play-song-list', datail.tracks);
        "
        />
      </ul>
      <h4>最新评论({{comments.length}})</h4>
      <PlayListComment
        v-for="items in comments"
        :key="items.id"
        :items="items"
      />
      </div>
    </div>
  </section>
</template>

<script>
import PlayListComment from '@/components/PlayListComment.vue';
import PlayListMusic from '@/components/PlayListMusic.vue';
export default {
  components: { PlayListMusic, PlayListComment },
  
  data: function () {
    return {
      comments: Object,
      datail:Object,
    };
  },
  created() {
    this.axios
      .get("https://music.kele8.cn/playlist/detail?id=" + this.$route.query.id)
      .then((res) => {
        this.datail = res.data.playlist;
      })
      .catch(function (error) {
        console.log(error);
      });

    this.axios
      .get("https://music.kele8.cn/comment/playlist", {
        params: {
          id: this.$route.query.id,
        },
      })
      .then((res) => {
        this.comments = res.data.hotComments;
      })
      .catch(function (error) {
        console.log(error);
      });
  },
  filters: {
    imgurl: function (picurl) {
      return picurl + "?imageView=1&type=webp&thumbnail=145x0";
    },
    parsePalyCount: function (number) {
      return number >= 10000
        ? parseFloat((number / 10000).toFixed(1)) + "万"
        : number;
    },
  },
};
</script>

<style lang="less" scoped>
.song-list {
  .top {
    // background: #eee;
    .header {
      width: 100%;
      height: 210px;
      background-repeat: no-repeat;
      background-size: cover;
      display: flex;
      position: relative;
      top: -84px;
      z-index: 1;
      &::after {
        content: "";
        display: block;
        width: 100%;
        height: 210px;
        background: rgba(0,0,0,.8);
        position: absolute;
        top: 0;
      }
      .leftpic {
        text-align: center;
        flex: 3;
        width: 80px;
        height: 100%;
        z-index: 1;
        position: relative;
        img {
          position: absolute;
          top: 35px;
          left: 20px;
          display: block;
          box-shadow: -0.1px -0.1px 0px #fff;
          // z-index: 21;
        }
        .numing {
          position: absolute;
          display: block;
          top: 35px;
          right: -4px;
          color: #fff;
          font-size: 12px;
          // background: yellow;
          // z-index: 1;
        }
      }
      .rightname {
        z-index: 1;
        position: relative;
        width: 220px;
        h2 {
          width: 210px;
          flex: 1;
          color: #fff;
          position: absolute;
          top: 33px;
          // right: 5px;
          font-size:15px;
          padding-left: 25px;
          padding-right: 5px;
          // margin-left: 45px;
          // background: red;
        }
      }
    }
    .song-list-content{
      margin-top: -84px;
    }
    h4{
      color: #666;
      font-size: 12px;
      background: #eeeff0;
      padding: 3px 10px;
    }
  }
}
</style>