<template>
    <section class="comment">
        <li class="commentlist">
            <div class="icon">
                <img 
                :src=items.user.avatarUrl|imgurl
                alt=""
                >
            </div>
            <div class="rightContent">
                <p class="name">
                    <span class="spanname">{{items.user.nickname}}</span>
                    <span class="spancount">
                        {{items.likedCount}}
                        <img src="../assets/like.png" alt="">
                    </span>
                </p>
                <p class="content">{{items.content}}</p>
            </div>
        </li>
    </section>
</template>

<script>
export default {
    props:["items"],
    filters: {
    imgurl: function (picurl) {
      return picurl + "?imageView=1&type=webp&thumbnail=145x0";
    },
  },
};
</script>

<style lang="less" scoped>
.comment{
    .commentlist{
        list-style: none;
        display: flex;
        padding: 10px 5px ;
        
        .icon{
            flex: 1;
            // background: red;
            text-align: center;
            img{
                width: 30px;
                height: 30px;
                border-radius: 50%;
            }
        }
        .rightContent{
            flex: 7;
            border-bottom: 1px solid #eee;
            padding-bottom: 10px;
            .name{
                position: relative;
                width: 100%;
                .spanname{
                    color: #666;
                    font-size: 14px;
                }
                .spancount{
                    position: absolute;
                    right: 10px;
                }
                
            }
            .content{
                padding-top: 10px;
                width: 330px;
            }
        }
    }
}
</style>