<template>

  <li
    class="current-play-list-item"
    @click="$emit('change-current-play-song', item)"
  >
    <div
      class="palying-animate"
      :class="{ paused: !playing }"
      v-if="currentPlaySong.id == item.id"
    >
      <span></span>
      <span></span>
      <span></span>
    </div>
    <div class="detail">
      <span>{{ item.name }} - </span>

      <span class="artist" v-for="artist in item.song ? item.song.artists : item.ar" :key="artist.id">
        {{ artist.name }}
      </span>
    </div>

    <div class="delete" @click.stop="$emit('remove-song',item.id)"><img src="../assets/delete.png" alt=""></div>
  </li>

</template>

<script>
export default {
  props: {
    item: Object,
    currentPlaySong: Object,
    playing: Boolean,
  },
};
</script>

<style lang="less">
li.current-play-list-item {
  display: flex;

  @keyframes shensuo {
    from {
      height: 20%;
    }
    to {
      height: 100%;
    }
  }
  .palying-animate {
    width: 20px;
    height: 20px;
    background: white;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;

    span {
      margin-left: 2px;
      width: 20%;
      height: 20%;
      background: rgb(208, 255, 0);
      animation: shensuo 0.6s linear infinite alternate;
      &:nth-child(1) {
        animation-delay: 0s;
      }
      &:nth-child(2) {
        animation-delay: -0.2s;
      }
      &:nth-child(3) {
        animation-delay: -0.4s;
      }
    }
    &.paused {
      span {
        animation-play-state: paused;
      }
    }
  }
  .detail {
    flex: 1; 
    line-height: 35px;  
    margin-left: 5px;
    font-size: 18px;
    .artist{
      font-size: 14px;
    }
  }
  .delete{
    margin-right: 10px;
    line-height: 35px;
  }
}
</style>