<template>
  <li class="musiclist-item" @click="$emit('change-current-play-song', item)">
    <div class="index">
      {{ index+1 }}
    </div>
    <div class="right">
      <div class="detail">
        <div class="name-alias">
          <h3>{{ item.name }}</h3>
        </div>
        <p>
          <span class="artist" v-for="ar in item.ar" :key="ar.id">
            {{ ar.name }}
          </span>
          <i class="album"> - {{ item.al.name }}</i>
        </p>
      </div>
      <div class="icon"></div>
    </div>
  </li>
</template>

<script>
export default {
  props: ["item", "index"],
};
</script>

<style lang="less" scoped>
li.musiclist-item {
  display: flex;
  align-items: center;
  // background-color: chartreuse;
  padding: 10px 0;

  padding-right: 10px;
  .index {
    width: 40px;
    height: 100%;
    text-align: center;
    color: #999;
  }
  .right{
    flex: 1;
    display: flex;
    border-bottom: 1px solid #eee;
    padding-bottom: 8px;
    // justify-items: center;
    align-items: center;
    .detail {
    flex: 1;
    .name-alias {
      display: -webkit-box;
      -webkit-line-clamp: 1;
      -webkit-box-orient: vertical;
      overflow: hidden;
      h3 {
        display: inline-block;
      }
      .alias {
        color: #888;
        margin-left: 4px;
      }
    }

