<template>
  <section class="playbar" :class="{ paused: !playing }">
    <img
    class="playbarimg"
      :src="`${
        currentPlaySong.picUrl || currentPlaySong.al.picUrl
      }?imageView=1&type=webp&thumbnail=60x0`"
      alt=""
    />
    <h5>{{ currentPlaySong.name }}</h5>
    <div class="progress">
      <span
        v-if="!playing"
        class="play-icon"
        @click.stop="$emit('audio-play')"
      ></span>
      <span v-else class="paused-icon" @click.stop="$emit('audio-pause')"
        ></span
      >
      <canvas ref="canvas" width="40" height="40"></canvas>
    </div>
    <div class="currentlist" @click.stop="$emit('show-list')"><img class="currentlistimg" src="../assets/list.png" alt=""></div>
  </section>
</template>

<script>
export default {
  props: {
    currentPlaySong: Object,
    playing: Boolean,
    duration: Number,
    currentTime: Number,
    circlePercent: Number,
  },

  methods: {
    drawCircle: function (n) {
      var canvas = this.$refs.canvas;
      const ctx = canvas.getContext("2d");
      // ctx.fillStyle = "green";

      ctx.clearRect(0, 0, 40, 40);
      // ctx.fillRect(0, 0, 40, 40);

      // ctx.strokeStyle = "#ddd";

      // ctx.beginPath();
      // ctx.arc(20, 20, 18, 0, 2 * Math.PI);
      // // ctx.closePath();
      // ctx.stroke();

      ctx.strokeStyle = "#dd001b";

      ctx.beginPath();
      ctx.arc(20, 20, 18, -0.5 * Math.PI, (2 * n - 0.5) * Math.PI);
      // ctx.closePath();
      ctx.stroke();

      ctx.strokeStyle = "#ccc";

      ctx.beginPath();

      ctx.arc(20, 20, 18, -0.5 * Math.PI, (2 * n - 0.5) * Math.PI, true);
      ctx.stroke();

      // console.log("xxxxxx", this);
    },
  },
  mounted() {
    this.drawCircle(this.circlePercent);
  },
  watch: {
    circlePercent: function (n, o) {
      // console.log(n);
      this.drawCircle(n);
    },
  },
};
</script>

<style lang="less" >
.playbar {
  width: 100%;
  height: 50px;
  background: white;
  position: fixed;
  bottom: 0;
  left: 0;

  display: flex;
  padding: 5px 10px;

  align-items: center;
  //   justify-content: space-between;

  box-shadow: 0 -1px 3px 0 rgba(0, 0, 0, 0.15);

  .playbarimg{
    border-radius: 50%;
    width: 40px;
    height: 40px;
    animation:disc-rotate 10s linear infinite;
  }
  &.paused {
    .playbarimg{
      animation-play-state: paused;
    }
  }
  h5 {
    flex: 1;
    padding: 0 10px;
  }
  .progress {
    position: relative;
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;

    span {
      &.play-icon {
        width: 10px;
        height: 10px;
        // background: red;
        overflow: hidden;

        &::before {
          content: "";
          width: 20px;
          height: 10px;
          display: block;
          // background: green;
          border-top: 5px solid transparent;
          border-right: 12px solid transparent;
          border-bottom: 5px solid transparent;
          border-left: 8px solid #ccc;
          margin-left: 3px;
        }
      }
      &.paused-icon{
        width: 10px;
        height: 10px;
        // background: red;
        overflow: hidden;
        &::before {
          content: "";
          width: 10px;
          height: 20px;
          display: block;
          // background: green;
         background: #ccc;
        }
         &::after {
          content: "";
          width: 10px;
          height: 20px;
          display: block;
          // background: green;
          background: #ccc;
        }
      }
    }

    canvas {
      position: absolute;
      top: 0;
      left: 0;
      z-index: -1;
      width: 100%;
      height: 100%;
    }
  }
  .currentlist{
    width: 25px;
    height: 25px;
    margin-left: 10px;
    .currentlistimg{
      width: 100%;
      height: 100%;
    }
  }
}
</style>